import { Fragment, useEffect, useRef } from "react";
import "./tr-digital-dentistry.style.scss";
import { ReactComponent as ArrowUp } from "../../../assets/check-1.svg";
const TRDigitalDentistry = () => {
  const digitalDentRef = useRef();
  const showDescription = (e) => {
    let num = 0;
    digitalDentRef.current.childNodes.forEach((nod) => {
      if (nod.childNodes[1].classList.contains("tr-digitaldent")) {
        num += 1;
      }
      nod.childNodes[0].childNodes[1].classList.remove("tr-digitaldent");
    });
    if (e.currentTarget.childNodes[1].classList.contains("tr-digitaldent")) {
      if (num > 1) {
        e.currentTarget.childNodes[1].classList.remove("tr-digitaldent");
        e.currentTarget.childNodes[1].classList.add("tr-digital-hidden");
        e.currentTarget.childNodes[0].childNodes[1].classList.remove(
          "tr-digit-svg"
        );
      }
    } else {
      e.currentTarget.childNodes[0].childNodes[1].classList.add("tr-digit-svg");
      e.currentTarget.childNodes[1].classList.add("tr-digitaldent");
      e.currentTarget.childNodes[1].classList.remove("tr-digital-hidden");
    }
  };
  useEffect(() => {
    document.title = "Digital Dentistry - BilimDent";
    window.scrollTo({ behavior: "smooth" }, 0, 0);
  });
  return (
    <Fragment>
      <div className="tr-digital-dentistry">
        <div className="tr-digital-dentistry-wrap">
          <div className="tr-digital-dentistry-header">
            <div className="tr-digital-dentistry-heading">
              DİGİTAL<span> DİŞ </span>HEKİMLİĞİ
            </div>
          </div>
          <div className="tr-digital-dentistry-container">
            <div className="tr-digital-dentistry-cont" ref={digitalDentRef}>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">
                    DİJİTAL DİŞ HEKİMLİĞİ NEDİR?
                  </span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digitaldent tr-digitalDent-back-1">
                  <p className="tr-digital-dentistry-description">
                    Dijital diş hekimliği, teşhis, planlama ve tedavi sırasında
                    tamamen veya kısmen bilgisayar destekli cihazların,
                    yöntemlerin ve teknolojinin kullanılması anlamına gelir.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Dijital diş hekimliği yöntemleri insan hatasını azaltıp,
                    üretim hassasiyetini artırmak ve tedavi sürelerini kısaltmak
                    amacıyla geliştirilmiş yöntemlerdir.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Basit bir dolgudan komplike implant tedavilerine, estetik
                    planlamadan gülüş tasarımına, ortodontiden porselen
                    kaplamalara kadar birçok uygulama artık dijital teknolojiler
                    ve cihazlar kullanılarak yapılabilmekte.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Dijital yöntemlerin hız ve üretim hassasiyeti gibi
                    avantajlarının yanında çok önemli bir avantajı da hastaya
                    üretilen dolgu, kaplama, köprü, ortodonti plakları, gece
                    plakları ve benzeri tüm uygulamaların üretim dataları veri
                    tabanlarında saklanabilmekte ve bir gün herhangi bir nedenle
                    tekrar üretilmesi gerektiğinde hastadan tekrar ölçü almadan
                    aynı data ile üretim yapılabiliyor olmasıdır.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Bir başka avantajı da hastanın mevcut ağız durumu taranıp
                    kaydedilebilmekte ve ileride yapılan yeni taramalar ile
                    karşılaştırılabiliyor olmasıdır. Bu sayede aşınmalar, yer
                    değiştirmeler ve benzeri değişikliklerin belirlenebiliyor
                    olmasıdır.
                  </p>
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">
                    DİJİTAL CERRAHİ REHBER (SURGİCAL GUİDED IMPLANTS)
                  </span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-2">
                  <p className="tr-digital-dentistry-description">
                    İmplant cerrahisi sırasında implantların çene kemiğine en
                    doğru açıda ve en doğru konumda yerleştirilmesini sağlayarak
                    implant cerrahisinin başarısını arttıran dijital cerrahi
                    rehber, diş eti kaldırılma gereksinimini ortadan kaldırarak
                    kanama miktarını azaltır.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Cerrahi travmaların azaltılmasını sağlayan dijital cerrahi
                    rehber uygulaması öncesi 3 boyutlu tomografi çekilerek
                    hastaların çene kemiği görüntüleri detaylı bir şekilde elde
                    edilir. Eksilen dişlerin bulunduğu kısımların analizi
                    gerçekleştirilir ve eksik dişi tamamlamak için
                    yerleştirilecek olan implantın boyutu, açısı ve konumu
                    dikkatli bir şekilde ayarlanır.
                  </p>
                  {/* <picture className="tr-digital-dentistry-image">
                    <img
                      width="600px"
                      height="420px"
                      src={require("../../../assets/digital-dentistry/surgical-guid.jpg")}
                    />
                  </picture> */}
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">LAZER DİŞ HEKİMLİĞİ</span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-3">
                  <p className="tr-digital-dentistry-description">
                    Lazer iyonize olmayan bir ışıktır ve dokuda birikim yapmaz.
                    Bu sebeple x ışını gibi zarar verecek bir özelliği de
                    yoktur. Uygun lazer dalga boyu kullanılarak doğru dozlarla
                    kullanımı önemlidir. Hamileler, çocuklar ve kalp hastaları
                    da dahil olmak üzere herkeste kullanılabilir. Lazer
                    kullanılan bölgede dezenfeksiyon ve sterilizasyon
                    sağlanmaktadır.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Gücü ayarlanabilen lazer ışınının çürük dokuyu seçici
                    davranması az madde kaybıyla tedavinin tamamlanmasını
                    sağlar. (Dişten sadece çürük bölgeyi kaldırır.)
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Lazer ile yapılan cerrahi uygulamalarda, işlem esnasında
                    neredeyse hiç kanama olmamaktadır. Her cerrahi uygulama
                    sonrasında ortaya çıkabilecek komplikasyon, şişme ve
                    rahatsızlık olasılıkları azalmakta ve daha hızlı bir
                    iyileşme sağlanmaktadır.
                  </p>
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">DİJİTAL 3D GÖRÜNTÜLEME</span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-4">
                  <p className="tr-digital-dentistry-description">
                    Günümüzde üç boyutlu gelişmiş tomografi röntgenleri ile
                    yaklaşık 30 saniye gibi bir sürede dişlerin ve önemli
                    anatomik yapıların haritasını çıkarabilmekteyiz. Bu sayede
                    gerek implant uygulamaları gerek estetik diş tedavisi
                    uygulamaları özenle planlanarak hiç risk almadan kusursuz
                    yapılabilmektedir.
                  </p>
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">DİJİTAL GÜLÜŞ TASARIMI</span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-5">
                  <p className="tr-digital-dentistry-description">
                    Diş hekimleri dijital yazılımlar sayesinde hastanın diş
                    yapısı, diş etleri, dudakları ve yüzü arasındaki en doğru
                    uyumu yakalayabiliyor. Böylece kişiye doğal ve estetik bir
                    gülüş oluşturulabiliyor. Dijital gülüş tasarımında hasta
                    odaklı bir tedavi yapılır. Her kişiye yakışan gülüş şekli
                    farklıdır. Bu yüzden diş hekimleri kişiye en uygun gülüşü
                    tasarlar.
                  </p>
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">DİJİTAL ÖLÇÜ VE TARAMA</span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-6">
                  <p className="tr-digital-dentistry-description">
                    Bazen klasik ve eskiden süre gelen geleneksel sistemle ölçü
                    alınması sırasında öğürme refleksi gerçekleşmektedir. Bu
                    durumda net ölçü alınamama gibi problemler yaşanabilir.
                    Dijital ölçü işleminde bu tip sorunlar minimum seviyede
                    yaşanabilmektedir. Alınan görüntüler sanal ortama aktarılıp
                    bilgisayar modellemesi yapılarak net uygulamalar yapmak
                    mümkün olur. Dijital ölçü sayesinde ölçü neredeyse kusursuz
                    olmakta ve hastada oluşabilicek rahatsızlıklar oldukça
                    azaltılmaktadır.
                  </p>
                  <p className="tr-digital-dentistry-description">
                    Dijital diş hekimliğinde optik bir kamera ile dişler taranır
                    ve 3 boyutlu olarak bilgisayar ortamına aktarılarak 3
                    boyutlu tarama yöntemi ile gerçekleştirilir.
                  </p>
                </div>
              </div>
              <div
                className="tr-digital-dentistry-desc"
                onClick={showDescription}
              >
                <div className="tr-digit-header">
                  <span className="tr-digit-head">İNLAP SİSTEMİ</span>
                  <span className="tr-digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="tr-digital-dentistry-w tr-digital-hidden tr-digitalDent-back-7">
                  <p className="tr-digital-dentistry-description">
                    Yeni inLab CAD Yazılımı, diş laboratuvarında CAD / CAM
                    sistemlerinin gereksinimlerine artık daha da uyumlu. Ayrı
                    bir CAD bileşeni olarak, yazılım tarama ve üretim
                    ünitesinden bağımsızdır. inLab CAD Yazılımı, optimize
                    tasarım süreçleri ve kullanıcı dostu arayüzü ile ihtiyaca
                    dayalı endikasyonları kapsar.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
export default TRDigitalDentistry;
