import { Fragment, useRef } from "react";
import { ReactComponent as Checkbox } from "../../../../assets/checkbox.svg";
import "./section-3.style.scss";
import { useNavigate } from "react-router-dom";

const Section3 = () => {
  const navigate = useNavigate();
  const onGetInfoHandler = () => {
    navigate("/contact");
  };
  const upDownBtnRef = useRef();
  const listRef = useRef();
  const upDownBtn = (e) => {
    if (listRef.current.scrollTop === 0) {
      listRef.current.scrollTop = listRef.current.clientHeight;
      upDownBtnRef.current.classList.add("up");
    } else {
      listRef.current.scrollTop = 0;
      upDownBtnRef.current.classList.remove("up");
    }
  };
  return (
    <Fragment>
      <section className="home-section-3">
        <div className="section-3-heading">Our Services</div>
        <div className="home-section-3-wrap">
          <div className="section-3-image-container">
            <img
              width="500"
              height="610"
              src={require("../../../../assets/bilimdent-dentists.jpg")}
            />
          </div>
          <div className="section-3-description">
            <div className="section-3-list" ref={listRef}>
              <div>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Pedodontics
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Digital Dentistry
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Endodontics
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Restorative Dentistry
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Jaw Joint
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Implantology
                </li>
              </div>
              <div>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Teeth Whitening
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Laminate Veneer
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Aesthetic Dentistry
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Pink Aesthetics
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Smile Design
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Orthodontics
                </li>
              </div>
            </div>
            <div
              onClick={upDownBtn}
              ref={upDownBtnRef}
              className="section-3-down-btn"
            >
              {/* &#10606; */}
              {/* more */}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                width="24px"
                height="24px"
                strokeWidth={1.5}
                stroke="#ba9e66"
                className="w-6 h-6"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M19.5 8.25l-7.5 7.5-7.5-7.5"
                />
              </svg>
            </div>
            <div className="section-3-info-button" onClick={onGetInfoHandler}>
              Get Information
            </div>
          </div>
        </div>
      </section>
    </Fragment>
  );
};
export default Section3;
