import { Fragment, useEffect, useRef, useState } from "react";
import "./tr-contact.style.scss";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";
import emailjs from "emailjs-com";

import { useForm } from "react-hook-form";
import { ReactComponent as Email } from "../../../assets/email.svg";
import { ReactComponent as Map } from "../../../assets/map.svg";
import { ReactComponent as Phone } from "../../../assets/phone.svg";
import { ReactComponent as Clock } from "../../../assets/clock.svg";

const TRContact = () => {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();
  const toastifySuccess = () => {
    toast("Form sent!", {
      position: "bottom-right",
      autoClose: 5000,
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: false,
      className: "submit-feedback success",
      toastId: "notifyToast",
    });
  };

  const onSubmit = async (e, data) => {
    e.preventDefault();
    const { name, email, subject, message } = data;
    try {
      const templateParams = {
        name,
        email,
        subject,
        message,
      };
      await emailjs.send(
        process.env.REACT_APP_SERVICE_ID,
        process.env.REACT_APP_TEMPLATE_ID,
        templateParams,
        process.env.REACT_APP_USER_ID
      );
      reset();
    } catch (e) {
      console.log(e);
    }
  };
  useEffect(() => {
    document.title = "Contact - BilimDent";

    window.scrollTo(0, 0);
    // submitRef.current.scrollIntoView({ behavior: "smooth" });
  });
  return (
    <Fragment>
      <div className="tr-contact">
        <h1 className="tr-contact-heading">
          İLET<span>İŞİM</span>
        </h1>
        <div className="tr-contact-container">
          <div className="tr-contact-wrap">
            <div className="tr-contact-wrap-container">
              <div className="tr-contact-cont">
                <div className="tr-contact-image">
                  <Email />
                </div>
                <div className="tr-contact-data">E-Posta</div>
                <div className="tr-contact-data-desc">
                  bilimdent@antalya.edu.tr
                </div>
              </div>
            </div>
            <div className="tr-contact-wrap-container">
              <div className="tr-contact-cont">
                <div className="tr-contact-image">
                  <Map />
                </div>
                <div className="tr-contact-data">Konum</div>
                <div className="tr-contact-data-desc">
                  Tahılpazarı, Mahallesi,
                  <br /> Kazım Özalp Cd. No 84 D:Kat 9,
                  <br /> 07040 Muratpaşa/Antalya
                </div>
              </div>
            </div>
            <div className="tr-contact-wrap-container">
              <div className="tr-contact-cont">
                <div className="tr-contact-image">
                  <Phone />
                </div>
                <div className="tr-contact-data">Telefon</div>
                <div className="tr-contact-data-desc">+90 (242) 245 00 11</div>
              </div>
            </div>
            <div className="tr-contact-wrap-container">
              <div className="tr-contact-cont">
                <div className="tr-contact-image">
                  <Clock />
                </div>
                <div className="tr-contact-data">Çalışma Saatleri</div>
                <div className="tr-contact-data-desc">
                  Pazartesi - Cuma: 08:00 - 21:00 Hafta Sonları: Kapalı
                </div>
              </div>
            </div>
          </div>
          <div className="tr-contact-container-2">
            <form className="tr-contact-form" onSubmit={handleSubmit(onSubmit)}>
              <div className="tr-contact-form-in">
                <label className="tr-contact-name">
                  Ad Soyad<span> *</span>
                </label>
                <input
                  type="text"
                  name="name"
                  {...register("name", {
                    required: {
                      value: true,
                      message: "Please enter your name",
                    },
                    maxLength: {
                      value: 30,
                      message: "Please use 30 characters or less",
                    },
                  })}
                />
                {errors.name && (
                  <span className="errorMessage">{errors.name.message}</span>
                )}
              </div>
              <div className="tr-contact-form-in">
                <label className="tr-contact-email">
                  E-Posta<span> *</span>
                </label>
                <input
                  type="text"
                  name="email"
                  {...register("email", {
                    required: true,
                    pattern:
                      /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/,
                  })}
                />
                {errors.email && (
                  <span className="errorMessage">
                    Please enter a valid email address
                  </span>
                )}
              </div>
              <div className="tr-contact-form-in">
                <label className="tr-contact-subj">
                  Konu<span> *</span>
                </label>
                <input
                  type="text"
                  name="subject"
                  {...register("subject", {
                    required: {
                      value: true,
                      message: "Please enter a subject",
                    },
                    maxLength: {
                      value: 75,
                      message: "Subject cannot exceed 75 characters",
                    },
                  })}
                />
                {errors.subject && (
                  <span className="errorMessage">{errors.subject.message}</span>
                )}
              </div>
              <div className="tr-contact-form-in tr-form-in-last">
                <label className="tr-contact-comment">
                  Mesaj<span> *</span>
                </label>
                <textarea
                  className="tr-text-area"
                  name="message"
                  type="text"
                  {...register("message", {
                    required: true,
                  })}
                />
                {errors.message && (
                  <span className="errorMessage">Please enter a message</span>
                )}
              </div>

              <button className="tr-contact-button" type="submit">
                Gönder
              </button>
            </form>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
export default TRContact;
