import { Fragment } from "react";
import "./section-1.style.scss";
import { Link } from "react-router-dom";
const Section1 = () => (
  <Fragment>
    <section className="home-section-1">
      {/* <div className="home-icons">
        <HomeIcons />
      </div> */}
      <div className="home-header">
        <div className="home-header-slogan">
          <span className="home-header-name">
            BILIM<span style={{ color: "#ba9e66" }}>DENT</span>
          </span>
          <br />
          <span className="home-header-name-2">
            {/* Smile scientifically with */}smile scientifically with us
          </span>
        </div>
        <div className="home-header-small">
          at least 10 years of professional experience
        </div>
        <a
          className="section-1-btn"
          target="_"
          href="https://bilimdentrandevu.antalya.edu.tr/"
        >
          <div className="home-button">
            Online Appointment
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              height="14px"
              width="14px"
              viewBox="0 0 24 24"
              strokeWidth={4}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M8.25 4.5l7.5 7.5-7.5 7.5"
              />
            </svg>
          </div>
        </a>
      </div>

      {/* <div className="home-container">
          <div className="home-image-2"></div>
        </div> */}
    </section>
  </Fragment>
);
export default Section1;
