import { Outlet, useNavigate } from "react-router-dom";
import "./tr-navigation.style.scss";
import { Fragment, useContext, useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import TRFooter from "../../tr-components/tr-footer/tr-footer.component";
import TRHomeIcons from "../../tr-components/tr-home-icons/tr-home-icons.component";
const TRNavigation = () => {
  const navOpenRef = useRef();
  const btnOpenClose = (e) => {
    if (navOpenRef.current?.classList[1]) {
      navOpenRef.current.classList.remove("tr-nav-open");
    } else {
      navOpenRef.current.classList.add("tr-nav-open");
    }
  };
  const navLinks = (e) => {
    if (e.target.className === "tr-nav-link") {
      if (navOpenRef.current?.classList[1] === "tr-nav-open") {
        navOpenRef.current?.classList.remove("tr-nav-open");
      }
    }
  };
  useEffect(() => {});
  return (
    <Fragment>
      <div className="tr-nav" ref={navOpenRef}>
        <Link to="/tr">
          <div className="tr-nav-logo">
            {/* <img src={require("../../../assets/Varlik-4.png")} /> */}
          </div>
        </Link>
        <nav className="tr-main-nav">
          <div className="tr-nav-links" onClick={navLinks}>
            <Link to="/tr">
              <div className="tr-nav-link">Anasayfa</div>
            </Link>
            <Link to="hakkimizda">
              <div className="tr-nav-link">Hakkımızda</div>
            </Link>
            <Link to="bilimdent-cocuk">
              <div className="tr-nav-link">Bilimdent Çocuk</div>
            </Link>
            <Link to="dijital-dis-hekimligi">
              <div className="tr-nav-link">Digital Diş Hekimliği</div>
            </Link>
            <Link to="blog">
              <div className="tr-nav-link" id="blog">
                Blog
              </div>
            </Link>
            <Link to="iletisim">
              <div className="tr-nav-link">İletişim</div>
            </Link>
            <Link to="/">
              <div className="tr-nav-link tr-tr" style={{ color: "#ba9e66" }}>
                EN
              </div>
            </Link>
          </div>
        </nav>
        <div className="tr-home-i">
          <TRHomeIcons />
        </div>
        <button className="tr-btn-mobile-nav" onClick={btnOpenClose}>
          <ion-icon class="icon-mobile-nav" name="menu-outline"></ion-icon>
          <ion-icon class="icon-mobile-nav" name="close-outline"></ion-icon>
        </button>
      </div>
      <Outlet />
      <TRFooter />
    </Fragment>
  );
};
export default TRNavigation;
