import { Fragment, useEffect, useRef } from "react";
import "./digital-dentistry.style.scss";
import { ReactComponent as ArrowUp } from "../../../assets/check-1.svg";
const DigitalDentistry = () => {
  const digitalDentRef = useRef();
  const showDescription = (e) => {
    // console.log(e.target.offsetParent);
    // e.currentTarget.lastChild.style.display = "block";
    // console.log(e.currentTarget.childNodes[1]);
    let num = 0;
    digitalDentRef.current.childNodes.forEach((nod) => {
      // nod.childNodes[1].classList.remove("digitaldent");
      if (nod.childNodes[1].classList.contains("digitaldent")) {
        num += 1;
      }
      nod.childNodes[0].childNodes[1].classList.remove("digitaldent");
    });
    if (e.currentTarget.childNodes[1].classList.contains("digitaldent")) {
      if (num > 1) {
        e.currentTarget.childNodes[1].classList.remove("digitaldent");
        e.currentTarget.childNodes[1].classList.add("digital-hidden");
        e.currentTarget.childNodes[0].childNodes[1].classList.remove(
          "digit-svg"
        );
      }
    } else {
      e.currentTarget.childNodes[0].childNodes[1].classList.add("digit-svg");
      e.currentTarget.childNodes[1].classList.add("digitaldent");
      e.currentTarget.childNodes[1].classList.remove("digital-hidden");
    }
  };
  useEffect(() => {
    document.title = "Digital Dentistry - BilimDent";
    window.scrollTo({ behavior: "smooth" }, 0, 0);
  });
  return (
    <Fragment>
      <div className="digital-dentistry">
        <div className="digital-dentistry-wrap">
          <div className="digital-dentistry-header">
            <div className="digital-dentistry-heading">
              DIGITAL <span>DENTISTRY</span>
            </div>
          </div>
          <div className="digital-dentistry-container">
            <div className="digital-dentistry-cont" ref={digitalDentRef}>
              <div className="digital-dentistry-desc" onClick={showDescription}>
                <div className="digit-header">
                  <span className="digit-head">WHAT IS DIGITAL DENTISTRY?</span>
                  <span className="digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="digital-dentistry-w digitaldent digitalDent-back-1">
                  <p className="digital-dentistry-description">
                    Digital dentistry refers to the use of fully or partially
                    computer-aided devices, methods and technology during
                    diagnosis, planning and treatment.
                  </p>
                  <p className="digital-dentistry-description">
                    Digital dentistry methods are methods developed to reduce
                    the margin of human error, increase production precision and
                    shorten treatment duration.
                  </p>
                  <p className="digital-dentistry-description">
                    From a simple filling to complicated implant treatments,
                    from aesthetic planning to smile design, from orthodontics
                    to porcelain veneers, many applications can now be performed
                    using digital technologies and devices.
                  </p>
                  <p className="digital-dentistry-description">
                    In addition to advantages such as speed and the sensitivity,
                    another significant advantage of digital production methods
                    is that production data for fillings, veneers, bridges,
                    orthodontic plaques, production plaques and all other
                    similar applications that are unique to the patient can be
                    stored in the database, and they can be produced again
                    without the need to take measurement again.
                  </p>
                  <p className="digital-dentistry-description">
                    Another advantage is that the patient’s current oral
                    condition can be scanned and recorded, and these recordings
                    can be used to make comparisons with new scans. In this way,
                    abrasions, displacements and similar changes can be
                    identified.
                  </p>
                  {/* <picture className="digital-dentistry-image">
                    <img
                      width="600px"
                      height="420px"
                      src={require("../../../assets/digital-dentistry/digital-dentistry.jpg")}
                    />
                  </picture> */}
                </div>
              </div>
              <div className="digital-dentistry-desc" onClick={showDescription}>
                <div className="digit-header">
                  <span className="digit-head">
                    DIGITAL SURGICAL GUIDE (SURGICAL GUIDED IMPLANTS)
                  </span>
                  <span className="digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="digital-dentistry-w digital-hidden digitalDent-back-2">
                  <p className="digital-dentistry-description">
                    Digital surgical guide, which increases the success of
                    implant surgery by ensuring that implants be placed at the
                    most accurate angle and in the most accurate position to the
                    jawbone during implant surgery, reduces the amount of
                    bleeding by eliminating the need for gum removal.
                  </p>
                  <p className="digital-dentistry-description">
                    Before digital surgical guide application, which enables
                    reducing surgical traumas, 3-dimensional tomography is taken
                    to obtained detailed images of the jawbone of the patient.
                    Then, an analysis is performed on the parts where the
                    missing teeth are located, and the size, angle and position
                    of the implant to be placed to replace the missing tooth are
                    carefully adjusted.
                  </p>
                  <p className="digital-dentistry-description">
                    Digital dentistry refers to the use of fully or partially
                    computer-aided devices, methods and technology during
                    diagnosis, planning and treatment.
                  </p>
                  <p className="digital-dentistry-description">
                    Digital dentistry methods are methods developed to reduce
                    the margin of human error, increase production precision and
                    shorten treatment duration.
                  </p>
                  <p className="digital-dentistry-description">
                    From a simple filling to complicated implant treatments,
                    from aesthetic planning to smile design, from orthodontics
                    to porcelain veneers, many applications can now be performed
                    using digital technologies and devices.
                  </p>
                  <p className="digital-dentistry-description">
                    In addition to advantages such as speed and the sensitivity,
                    another significant advantage of digital production methods
                    is that production data for fillings, veneers, bridges,
                    orthodontic plaques, production plaques and all other
                    similar applications that are unique to the patient can be
                    stored in the database, and they can be produced again
                    without the need to take measurement again.
                  </p>
                  <p className="digital-dentistry-description">
                    Another advantage is that the patient’s current oral
                    condition can be scanned and recorded, and these recordings
                    can be used to make comparisons with new scans. In this way,
                    abrasions, displacements and similar changes can be
                    identified.
                  </p>
                  {/* <picture className="digital-dentistry-image">
                    <img
                      width="600px"
                      height="420px"
                      src={require("../../../assets/digital-dentistry/surgical-guid.jpg")}
                    />
                  </picture> */}
                </div>
              </div>
              <div className="digital-dentistry-desc" onClick={showDescription}>
                <div className="digit-header">
                  <span className="digit-head">LASER DENTISTRY</span>
                  <span className="digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="digital-dentistry-w digital-hidden digitalDent-back-3">
                  <p className="digital-dentistry-description">
                    Laser is non-ionizing light and does not accumulate in the
                    tissue. For this reason, it does not have any damaging
                    properties such as x-ray. It is important to use it in the
                    right dose using appropriate laser wavelength. It can be
                    used on everyone, including pregnant women, children, and
                    heart patients.
                  </p>
                  <p className="digital-dentistry-description">
                    Disinfection and sterilization are applied in the area where
                    the laser is used.
                  </p>
                  <p className="digital-dentistry-description">
                    The selective property of the laser beam, the power of which
                    can be adjusted, on the carious tissue ensures that the
                    treatment be completed with minimal loss of substance.
                    (Removes only the carious area from the tooth.)
                  </p>
                  <p className="digital-dentistry-description">
                    In surgical applications performed with laser, there is
                    almost no bleeding during the procedure. The possibility of
                    complications, swelling and discomfort that may occur after
                    each surgical application is reduced, and a faster recovery
                    is ensured.
                  </p>
                  {/* <picture className="digital-dentistry-image">
                    <img
                      width="600px"
                      height="420px"
                      src={require("../../../assets/digital-dentistry/laser-dentistry.jpg")}
                    />
                  </picture> */}
                </div>
              </div>
              <div className="digital-dentistry-desc" onClick={showDescription}>
                <div className="digit-header">
                  <span className="digit-head">DIGITAL 3D IMAGING</span>
                  <span className="digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="digital-dentistry-w digital-hidden digitalDent-back-4">
                  <p className="digital-dentistry-description">
                    Nowadays, with advanced three-dimensional tomographic
                    x-rays, we can map out the teeth and important anatomical
                    structures in about 30 seconds. In this way, both implant
                    applications and aesthetic dental treatment applications can
                    be performed flawlessly without any taking risks thanks to
                    careful planning.
                  </p>
                  {/* <picture className="digital-dentistry-image">
                    <img
                      width="600px"
                      height="420px"
                      src={require("../../../assets/digital-dentistry/digital-3d-imaging.jpg")}
                    />
                  </picture> */}
                </div>
              </div>
              <div className="digital-dentistry-desc" onClick={showDescription}>
                <div className="digit-header">
                  <span className="digit-head">DIGITAL SMILE DESIGN</span>
                  <span className="digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="digital-dentistry-w digital-hidden digitalDent-back-5">
                  <p className="digital-dentistry-description">
                    Thanks to digital software, dentists can find the most
                    accurate alignment between the patient’s tooth structure,
                    gums, lips and face. Thus, a natural and aesthetic smile can
                    be created for a person.
                  </p>
                  <p className="digital-dentistry-description">
                    In digital dentistry, teeth are scanned with an optical
                    camera, and images are transferred to a computer environment
                    in 3 dimensions. Measurements are taken using the
                    3-dimensional scanning method.
                  </p>

                  {/* <picture className="digital-dentistry-image">
                    <img
                      width="600px"
                      height="420px"
                      src={require("../../../assets/digital-dentistry/smile.jpg")}
                    />
                  </picture> */}
                </div>
              </div>
              <div className="digital-dentistry-desc" onClick={showDescription}>
                <div className="digit-header">
                  <span className="digit-head">
                    DIGITAL MEASUREMENT AND SCANNING
                  </span>
                  <span className="digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="digital-dentistry-w digital-hidden digitalDent-back-6">
                  <p className="digital-dentistry-description">
                    Sometimes a gag reflex occurs while taking measurements
                    using the classical and traditional system, which leads to
                    problems such inability to take precise measurements. Such
                    problems can be experienced at a minimum level in the
                    digital measurement process. It is possible to make clear
                    implementations by transferring the obtained images to
                    virtual environment and performing computer modeling. Thanks
                    to digital measurement method, the measurement is almost
                    perfect, and discomfort likely to be suffered by the patient
                    is greatly reduced. In digital dentistry, teeth are scanned
                    with an optical camera, and images are transferred to a
                    computer environment in 3 dimensions. Measurements are taken
                    using the 3-dimensional scanning method.
                  </p>
                  {/* <picture className="digital-dentistry-image">
                    <img
                      width="600px"
                      height="420px"
                      src={require("../../../assets/digital-dentistry/digital-measurement-and-scanning.jpg")}
                    />
                  </picture> */}
                </div>
              </div>
              <div className="digital-dentistry-desc" onClick={showDescription}>
                <div className="digit-header">
                  <span className="digit-head">INLAB SYSTEM</span>
                  <span className="digit-svg-down">
                    <ArrowUp />
                  </span>
                </div>
                <div className="digital-dentistry-w digital-hidden digitalDent-back-7">
                  <p className="digital-dentistry-description">
                    The new InLab CAD Software is now even more compatible with
                    the requirements of CAD / CAM systems in dental
                    laboratories. As a separate CAD component, the software is
                    independent of the scanning and production unit. InLab CAD
                    Software covers need-based indications with optimized design
                    processes and a user-friendly interface.
                  </p>
                  {/* <picture className="digital-dentistry-image">
                    <img
                      width="600px"
                      height="420px"
                      src={require("../../../assets/digital-dentistry/inlab systems.jpg")}
                    />
                  </picture> */}
                </div>
              </div>
            </div>
            {/* <div className="digital-dentistry-image-cont"></div> */}
          </div>
        </div>
      </div>
    </Fragment>
  );
};
export default DigitalDentistry;
