import { Fragment, useRef } from "react";
import { ReactComponent as Checkbox } from "../../../../assets/checkbox.svg";
import "./tr-section-3.style.scss";
import { useNavigate } from "react-router-dom";

const TRSection3 = () => {
  const navigate = useNavigate();
  const onGetInfoHandler = () => {
    navigate("/contact");
  };
  const upDownBtnRef = useRef();
  const listRef = useRef();
  const upDownBtn = (e) => {
    if (listRef.current.scrollTop === 0) {
      listRef.current.scrollTop = listRef.current.clientHeight;
      upDownBtnRef.current.classList.add("up");
    } else {
      listRef.current.scrollTop = 0;
      upDownBtnRef.current.classList.remove("up");
    }
  };
  return (
    <Fragment>
      <section className="tr-home-section-3">
        <div className="tr-section-3-heading">Hizmetlerimiz</div>
        <div className="tr-home-section-3-wrap">
          <div className="tr-section-3-image-container">
            <img
              width="500"
              height="610"
              src={require("../../../../assets/bilimdent-dentists.jpg")}
            />
          </div>
          <div className="tr-section-3-description">
            <div className="tr-section-3-list" ref={listRef}>
              <div>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Pedodonti
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Dijital Diş Hekimliği
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Endodonti
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Restoratif Detistry
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Çene Eklemi
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  İmplantoloji
                </li>
              </div>
              <div>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Diş beyazlatma
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Laminat Kaplama
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Estetik Diş Hekimliği
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Pembe Estetik
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Gülüş Tasarımı
                </li>
                <li>
                  <div>
                    <Checkbox />
                  </div>
                  Ortodonti
                </li>
              </div>
            </div>
            <div
              onClick={upDownBtn}
              ref={upDownBtnRef}
              className="tr-section-3-down-btn"
            >
              {/* &#10606; */}
              {/* more */}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                width="24px"
                height="24px"
                strokeWidth={1.5}
                stroke="#ba9e66"
                className="w-6 h-6"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M19.5 8.25l-7.5 7.5-7.5-7.5"
                />
              </svg>
            </div>
          </div>
        </div>
      </section>
    </Fragment>
  );
};
export default TRSection3;
