import { Fragment } from "react";
import "./tr-section-1.style.scss";
import { Link } from "react-router-dom";
const TRSection1 = () => (
  <Fragment>
    <section className="tr-home-section-1">
      {/* <div className="tr-home-icons">
        <HomeIcons />
      </div> */}
      <div className="tr-home-header">
        <div className="tr-home-header-slogan">
          <span className="tr-home-header-name">
            BİLİM<span style={{ color: "#ba9e66" }}>DENT</span>
          </span>
          <br />
          <span className="tr-home-header-name-2">
            {/* Smile scientifically with */} ile bilimsel gülümseyin
          </span>
        </div>
        <div className="tr-home-header-small">en az 10 yıl mesleki tecrübe</div>
        <a
          className="tr-section-1-btn"
          target="_"
          href="https://bilimdentrandevu.antalya.edu.tr/"
        >
          <div className="tr-home-button">
            Online Randevu
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              height="14px"
              width="14px"
              viewBox="0 0 24 24"
              strokeWidth={4}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M8.25 4.5l7.5 7.5-7.5 7.5"
              />
            </svg>
          </div>
        </a>
      </div>

      {/* <div className="tr-home-container">
          <div className="tr-home-image-2"></div>
        </div> */}
    </section>
  </Fragment>
);
export default TRSection1;
