import TRSection1 from "../../tr-components/tr-home-sections/tr-section-1/tr-section-1.component";
import TRSection2 from "../../tr-components/tr-home-sections/tr-section-2/tr-section-2.component";
import TRSection3 from "../../tr-components/tr-home-sections/tr-section-3/tr-section-3.component";
import TRSection4 from "../../tr-components/tr-home-sections/tr-section-4/tr-section-4.component";
import TRSection5 from "../../tr-components/tr-home-sections/tr-section-5/tr-section-5.component";
import TRSection8 from "../../tr-components/tr-home-sections/tr-section-8/tr-section-8.component";
import "./tr-home.style.scss";

import { useEffect } from "react";
const TRHome = () => {
  useEffect(() => {
    document.title = "BilimDent";
    window.scrollTo({ behavior: "smooth" }, 0, 0);
  });
  return (
    <div className="tr-home">
      <TRSection1 />
      <TRSection2 />
      <TRSection3 />
      <TRSection5 />
      <TRSection4 />
      <TRSection8 />
    </div>
  );
};
export default TRHome;
