import { Fragment, useEffect } from "react";
import "./tr-blog.style.scss";
import { useNavigate } from "react-router-dom";
const TRBlog = () => {
  const navigate = useNavigate();
  useEffect(() => {
    document.title = "Blog - BilimDent";
    window.scrollTo({ behavior: "smooth" }, 0, 0);
  });
  const goToAntalya = () => {
    navigate("antalya-tr");
  };
  const goToImplants = () => {
    navigate("dis-implantlari-sonrasi-bakim");
  };
  const goToFAQ = () => {
    navigate("sss");
  };
  return (
    <Fragment>
      <div className="tr-blog">
        <div className="tr-blog-header">
          <div>Blog</div>
        </div>
        <div className="tr-blog-wrap">
          <div className="tr-blog-wrap-image"></div>
          <div className="tr-blog-content">
            <div className="tr-blog-content-1" onClick={goToAntalya}>
              <div className="tr-blog-content-image"></div>
              <div className="tr-blog-content-header">
                <div>Antalya</div>
              </div>
            </div>
            <div className="tr-blog-content-2" onClick={goToImplants}>
              <div className="tr-blog-content-image-2"></div>
              <div className="tr-blog-content-header">
                <div>İmplantları sonrası bakım</div>
                {/* <p>
                  It is possible to have some pain after your treatment is
                  completed; however it is not a problem as your mouth might be
                  sore. As you are prescribed with pain killers or antibiotics,
                  please pay attention to use them regularly as they help you
                  deal with the pain.
                </p> */}
              </div>
            </div>
            <div className="tr-blog-content-3" onClick={goToFAQ}>
              <div className="tr-blog-content-image-3"></div>
              <div className="tr-blog-content-header">
                <div>SSS</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
export default TRBlog;
