import { Fragment, useEffect, useRef, useState } from "react";
import "./section-4.style.scss";
import { ReactComponent as Check } from "../../../../assets/check-2.svg";
const Section4 = () => {
  const [n, setN] = useState(1);
  const [numberImg, setNumberImg] = useState(5);
  const [pix, setPix] = useState(0);
  const wrapperRef = useRef();
  const galleryRef = useRef();
  const transformRef = useRef();
  const imageContainer = useRef();
  const buttonLeft = () => {
    let childWidth = parseInt(imageContainer.current?.firstChild?.offsetWidth);
    let parentWidth = parseInt(imageContainer.current?.offsetWidth);
    if (n > 1) {
      let num = n - 1;
      setN(num);
      let width = pixels - parseInt(childWidth);
      setPixels(width);
      imageContainer.current.scrollLeft = width;
    } else {
      setN(5);
      setPixels(childWidth * 4);
      imageContainer.current.scrollLeft = childWidth * 4;
    }
  };
  const buttonRight = () => {
    let childWidth = parseInt(imageContainer.current?.firstChild?.offsetWidth);
    if (n < 5) {
      let num = n + 1;
      setN(num);
      let width = pixels + parseInt(childWidth);
      setPixels(width);
      imageContainer.current.scrollLeft = width;
      console.log(width);
    } else {
      setN(1);
      setPixels(0);
      imageContainer.current.scrollLeft = 0;
    }
  };
  const buttonRef = useRef();
  const openModal = (e) => {
    let childWidth = parseInt(imageContainer.current?.firstChild?.offsetWidth);
    let num = parseInt(e.target.className.slice(-1));
    wrapperRef.current.classList.add("gap");
    if (window.innerWidth > 704) {
      if (num === 6) {
        imageContainer.current.scrollLeft = childWidth * (num - 2);
        setN(num - 1);
        setPixels(childWidth * (num - 2));
        viewGallery();
        return;
      }
    }
    imageContainer.current.scrollLeft = childWidth * (num - 1);
    setN(num);
    setPixels(childWidth * (num - 1));
    viewGallery();
  };
  const closeModal = () => {
    wrapperRef.current.classList.remove("gap");
    transformRef.current.style.transform = "translateY(0px)";
    buttonRef.current.classList.remove("section-4-button-hidden");
  };
  const [isDragStart, setIsDragStart] = useState(false);
  const [isDragging, setIsDragging] = useState(false);
  const [prevPageX, setPrevPageX] = useState();
  const [prevScrollLeft, setPrevScrollLeft] = useState();
  const [positionDiff, setPositionDiff] = useState();
  const [pixels, setPixels] = useState(0);
  const galleryButtons = useRef();
  const autoSlider = () => {
    if (
      imageContainer.current.scrollLeft -
        (imageContainer.current.scrollWidth -
          imageContainer.current.clientWidth) >
        -1 ||
      imageContainer.current.scrollLeft <= 0
    ) {
      return;
    }
    let firstImgWidth = imageContainer.current.firstChild.clientWidth;
    if (imageContainer.current.scrollLeft > prevScrollLeft) {
      setPixels(firstImgWidth + pixels);
      let num = n + 1;
      setN(num);
      console.log(firstImgWidth * n);
      galleryButtons.current.childNodes[n]?.classList.add("aaa");
      galleryButtons.current.childNodes[n - 1]?.classList.remove("aaa");
      return (imageContainer.current.scrollLeft = firstImgWidth * n);
      // return buttonRight;
    }
    setPixels(Math.abs(pixels - firstImgWidth));
    let num = n - 1;
    setN(num);
    console.log(num);
    galleryButtons.current.childNodes[num - 1]?.classList.add("aaa");
    galleryButtons.current.childNodes[num]?.classList.remove("aaa");
    imageContainer.current.scrollLeft = firstImgWidth * (num - 1);
    // buttonLeft();
  };
  const dragStart = (e) => {
    setIsDragStart(true);
    setPrevPageX(e.pageX || e.touches[0].pageX);
    setPrevScrollLeft(imageContainer.current.scrollLeft);
  };
  const dragMove = (e) => {
    if (!isDragStart) return;
    e.preventDefault();
    console.log(`firing`);
    setIsDragging(true);
    imageContainer.current.classList.add("dragging");
    setPositionDiff((e.pageX || e.touches[0].pageX) - prevPageX);
    imageContainer.current.scrollLeft =
      prevScrollLeft - ((e.pageX || e.touches[0].pageX) - prevPageX);
  };
  const dragEnd = (e) => {
    setIsDragStart(false);
    imageContainer.current.classList.remove("dragging");
    if (!isDragging) return;
    setIsDragging(false);
    autoSlider();
  };
  const viewGallery = (e) => {
    wrapperRef.current.classList.add("gap");
    buttonRef.current.classList.add("section-4-button-hidden");
    transformRef.current.style.transform = "translateY(-500px)";
  };
  const galleryButtonsActive = (e) => {
    if (e.target.classList[0] === "g-buttons") {
      e.currentTarget.childNodes.forEach((nodes) => {
        nodes.classList.remove("aaa");
      });
      e.target.classList.add("aaa");
      imageContainer.current.scrollLeft =
        imageContainer.current.firstChild.clientWidth * (e.target.id - 1);
    }
  };
  useEffect(() => {
    if (window.innerWidth < 704) {
      setNumberImg(6);
    } else {
      setNumberImg(5);
      const eventListenerFunc = (e) => {
        if (e.key === "ArrowRight") {
          buttonRight();
        } else if (e.key === "ArrowLeft") {
          buttonLeft();
        } else if (e.key === "Escape") {
          closeModal();
        }
      };
      window.addEventListener("keydown", eventListenerFunc);
      return () => window.removeEventListener("keydown", eventListenerFunc);
    }
  }, [window.innerWidth]);
  useEffect(() => {
    imageContainer.current.addEventListener("touchmove", dragMove, {
      passive: false,
    });
    // imageContainer.current.addEventListener("touchend", dragEnd, {
    //   passive: false,
    // });
  });
  return (
    <Fragment>
      <section className="home-section-4">
        <div className="home-section-4-wrapper" ref={wrapperRef}>
          <div className="section-4-frame">
            <div className="section-4-inner" ref={transformRef}>
              <div
                className="section-4-boxes"
                ref={galleryRef}
                onClick={openModal}
              >
                <div className="box hoverfunc section-4-img-1"></div>
                <div className="box hoverfunc section-4-img-2"></div>
                <div className="box hoverfunc section-4-img-3"></div>
                <div className="box hoverfunc section-4-img-4"></div>
                <div className="box hoverfunc section-4-img-5"></div>
                <div className="box hoverfunc section-4-img-6"></div>
                {/* <div className="box hoverfunc section-4-img-7"></div> */}
              </div>

              <div className="section-4-image-container">
                <div
                  className="section-4-image"
                  ref={imageContainer}
                  onTouchStart={dragStart}
                  // onTouchMove={dragMove}
                  onTouchEnd={dragEnd}
                  // onMouseDown={dragStart}
                  // onMouseMove={dragMove}
                  // onMouseUp={dragEnd}
                >
                  <img
                    id="1"
                    draggable="false"
                    src={require("../../../../assets/gallery-1.jpg")}
                  />
                  <img
                    id="2"
                    draggable="false"
                    src={require("../../../../assets/gallery-2.jpg")}
                  />
                  <img
                    id="3"
                    draggable="false"
                    src={require("../../../../assets/gallery-3.jpg")}
                  />
                  <img
                    id="4"
                    draggable="false"
                    src={require("../../../../assets/gallery-4.jpg")}
                  />
                  <img
                    id="5"
                    draggable="false"
                    src={require("../../../../assets/gallery-5.jpg")}
                  />
                  <img
                    id="6"
                    draggable="false"
                    src={require("../../../../assets/gallery-6.jpg")}
                  />
                </div>
              </div>
              <div
                className="gallery-buttons"
                ref={galleryButtons}
                onClick={galleryButtonsActive}
              >
                <div id="1" className="g-buttons aaa"></div>
                <div id="2" className="g-buttons"></div>
                <div id="3" className="g-buttons"></div>
                <div id="4" className="g-buttons"></div>
                <div id="5" className="g-buttons"></div>
                <div id="6" className="g-buttons"></div>
              </div>
              <div className="section-4-exit" onClick={closeModal}>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="w-6 h-6"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M6 18L18 6M6 6l12 12"
                  />
                </svg>
              </div>
              <div className="section-4-details">
                <div
                  className="section-4-details-button-left"
                  onClick={buttonLeft}
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    width="40px"
                    height="40px"
                    viewBox="0 0 24 24"
                    strokeWidth="1.5"
                    stroke="currentColor"
                    className="w-6 h-6"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M15.75 19.5L8.25 12l7.5-7.5"
                    />
                  </svg>
                </div>
                <div className="section-4-details-num">
                  {n}/{numberImg}
                </div>
                <div
                  className="section-4-details-button-right"
                  onClick={buttonRight}
                >
                  {" "}
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    width="40px"
                    height="40px"
                    viewBox="0 0 24 24"
                    strokeWidth={1.5}
                    stroke="currentColor"
                    className="w-6 h-6"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M8.25 4.5l7.5 7.5-7.5 7.5"
                    />
                  </svg>
                </div>
              </div>
            </div>
          </div>
          <div
            className="section-4-button"
            ref={buttonRef}
            onClick={viewGallery}
          >
            Explore The Gallery
          </div>
        </div>
      </section>
    </Fragment>
  );
};
export default Section4;
