import { Fragment, useEffect } from "react";
import "./blog.style.scss";
import { useNavigate } from "react-router-dom";
const Blog = () => {
  const navigate = useNavigate();
  useEffect(() => {
    document.title = "Blog - BilimDent";
    window.scrollTo({ behavior: "smooth" }, 0, 0);
  });
  const goToAntalya = () => {
    navigate("antalya");
  };
  const goToImplants = () => {
    navigate("dental-implants-aftercare");
  };
  const goToFAQ = () => {
    navigate("faq");
  };
  return (
    <Fragment>
      <div className="blog">
        <div className="blog-header">
          <div>Blog</div>
        </div>
        <div className="blog-wrap">
          <div className="blog-wrap-image"></div>
          <div className="blog-content">
            <div className="blog-content-1" onClick={goToAntalya}>
              <div className="blog-content-image"></div>
              <div className="blog-content-header">
                <div>Antalya</div>
                {/* <p>
                  Antalya is in touch with nature, history, uniqe and sunny
                  coastlines of the Mediterranen with world-famous destinations.
                  Just a few hours from Europe you able to reach to magnificent
                  Mediterranean history, culture, shopping advantages.
                </p> */}
              </div>
            </div>
            <div className="blog-content-2" onClick={goToImplants}>
              <div className="blog-content-image-2"></div>
              <div className="blog-content-header">
                <div>Dental Implants Aftercare</div>
                {/* <p>
                  It is possible to have some pain after your treatment is
                  completed; however it is not a problem as your mouth might be
                  sore. As you are prescribed with pain killers or antibiotics,
                  please pay attention to use them regularly as they help you
                  deal with the pain.
                </p> */}
              </div>
            </div>
            <div className="blog-content-3" onClick={goToFAQ}>
              <div className="blog-content-image-3"></div>
              <div className="blog-content-header">
                <div>FAQ</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
export default Blog;
